
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { mapGetters } from 'vuex'

const list = () => [
{
title: '首页',
url: '/',
icon: 'fa fa-globe s27',
},
{
title: '任务记录',
url: '/task-note',
icon: 'fa fa-tasks s27',
},
{
title: '公告动态',
url: '/situation',
icon: 'fa fa-bell s27',
},
{
title: '我的',
url: '/user',
icon: 'fa fa-user s27',
},

];

export default{
name: 'AppFooter',
components: {
Tabbar,
TabbarItem,
},
data(){
return{
index:0,
tag_list: list(),
}
},
computed: {
...mapGetters({
getCurrentRouterPath: 'getCurrentRouterPath'
}),
},
methods:{
getTagInfobyURL(url) {
for(let n in this.tag_list) {
if(this.tag_list[n].url === url) {
return this.tag_list[n];
}
}
return false;
}
}

}




