
import AppHeader from '../components/Header.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import api from '../utils/api'
import * as _ from '../utils/tool'

export default {
components: {
AppHeader,
Group,
XInput,
XButton,
Radio,
},
data: function () {
return {
verify_code_src: '/verifycode/captcha',
options: [
{
key: 1,
value: '通过手机验证码找回密码'
},
{
key: 2,
value: '通过邮箱验证码找回密码'
},
],
type: 1,                //找回密码类型 1手机 2邮箱
account: '',            //用户账号
verify_code:'',         //图片验证码
verify_server_code: '', //手机或邮件验证码
password: '',           //新密码
}
},
computed: {
verify_code_title: function () {
if(this.type === 1) {
return '手机验证码'
}
return '邮箱验证码'
},
verify_type: function () {
if(this.type === 1) {
return '手机'
}
return '邮箱'
}
},
watch: {
type: function (val, old) {
this.account = '';
this.verify_code = '';
this.verify_code_src = '/verifycode/captcha?t=' + Math.random()
}
},
methods: {
sendPhoneCode: function () {
let params = {
username: this.account,
phone: this.account,
email: this.account,
type: this.type,
code: this.verify_code,
};

if(!params.type) {
_.toast('找回密码的类型不能为空！');
return false;
}
if(!params.username) {
_.toast('找回密码的账号不能为空，可以是手机号/邮箱/用户名！');
return false;
}
if(!params.code) {
_.toast('图片验证码不能为空！');
return false;
}


if(this.type === 1) {

api.getMobileCode(params).then(
res => {
_.toast(this.verify_code_title + '已经发送！')
},
p => {
this.verify_code_src = '/verifycode/captcha?t=' + Math.random()
}
)
}

if(this.type === 2) {

api.getEmailCode(params).then(
res => {
_.toast(this.verify_code_title + '已经发送！')
},
p => {
this.verify_code_src = '/verifycode/captcha?t=' + Math.random()
}
)
}
},
forgetPassword: function () {
let params = {
username: this.account,
phone: this.account,
email: this.account,
type: this.type,
code: this.verify_code,
phone_vcode: this.verify_server_code,
email_vcode: this.verify_server_code,
password: this.password
};

if(params.password.length < 6) {
_.toast('新密码长度不能小于6！');
return false;
}
if(!params.type) {
_.toast('找回密码的类型不能为空！');
return false;
}
if(!params.username) {
_.toast('找回密码的账号不能为空，可以是手机号/邮箱/用户名！');
return false;
}
if(!params.code) {
_.toast('图片验证码不能为空！');
return false;
}
if(!this.verify_server_code) {
_.toast(this.verify_code_title + '不能为空！');
return false;
}

api.postForgetPassword(params).then(
res => {
_.toast(res.message ? res.message : '密码修改成功！');
_.go('/', this.$router)
},
p => {

}
)
},
change: function (type) {

}
}
}



