
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'

import { mapGetters } from 'vuex'
import * as _ from '../utils/tool'
export default {
name: 'AppAnnouncement',
components: {
Marquee,
MarqueeItem,
},
data: function () {
return {

}
},
computed: {
...mapGetters({
note_list: 'getRollingAnnouncement'
})

},
methods: {
onClick: function (item) {
if(item.id > 0) {
_.go('/article-detail/' + item.id, this.$router)
}
}
}
}



