
import AppHeader from '../components/Header.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import * as _ from '../utils/tool'
import api from '../utils/api'

export default {
directives: {
TransferDom
},
components: {
AppHeader,
XHeader,
XDialog,
Radio,
},
data: function () {
return {
show_dialog: false,
cash_money: 10,
profile: {},
options: [
{
key: 10,
value: '10元'
},
{
key: 20,
value: '20元'
},
{
key: 50,
value: '50元'
},
{
key: 100,
value: '100元'
},
{
key: 1000,
value: '1000元'
},
]
}
},
mounted: function () {
this.getProfileInfo();
},
methods: {
goList: function () {
_.go('/user/enchashment-list', this.$router)
},
submit: function () {
if(!this.cash_money || isNaN(this.cash_money)) {
_.toast('提现金额不正确,请重新选择');
return false;
}
if(parseFloat(this.cash_money) > parseFloat(this.profile.money)) {
_.toast('可提现金额不足，请重新选择');
return false;
}
api.postWorkerWithdrawal(this.cash_money).then(
res => {
this.show_dialog = false;
this.profile.money = this.profile.money - this.cash_money

this.$vux.alert.show({
content: '提现申请成功，请查看您的收款账户以确认是否到帐',
})
},
p_obj => {
_.toast(p_obj.data.hasOwnProperty('message') ? p_obj.data.message : '数据返回异常，请稍后再试或联系客服', 'text', 3000, 'middle');
}
)

},
change: function (val) {
this.cash_money = val;
},
getProfileInfo: function () {
api.getProfile().then(
res => {
if(!$.isEmptyObject(res.data)) {
this.profile = res.data;
}
},
p_obj => {

}
);
},
formatYen: function (yen) {
return _.formatYen(yen)
}
}
}



