
import FastClick from 'fastclick'
import App from './App'
import store from './vuex/store'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import router from './router/router'
import * as _ from './utils/tool'



import VueScroller from 'vue-scroller'
Vue.use(VueScroller)

import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)




Vue.directive('focus', {

inserted: function (el) {

el.focus()
}
});

Vue.use(ToastPlugin);
FastClick.attach(document.body)


router.beforeEach(function (to, from, next) {
store.commit('updateIsLoading', { is_loading: true });
if (/\/http/.test(to.path)) {
_.go(to.path, router)
} else {
next()
}
});

router.afterEach(function (to,from) {
store.commit('updateIsLoading', { is_loading: false });

store.commit('updatePreRouterPath', { pre_router_path: from.path });

store.commit('updateCurrentRouterPath', { current_router_path: to.path })

store.commit('updateNextRouterPath', { next_router_path: false })


window.scrollTo(0,0)
});


/* eslint-disable no-new */
new Vue({
store,
router,
render: h => h(App)
}).$mount('#app-box');



