
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import * as _ from '../utils/tool'
import api from '../utils/api'

export default {
components: {
XDialog,
Radio,
},
directives: {
TransferDom
},
data: function () {
return {
height:         window.innerHeight * 0.6,
show: false,
selected: '',
}
},
props: {
options: {
default: [
'我是认真完成并且按照要求截图并上传的!',
]
},
submit_function: {   //取消任务执行的函数,返回第一个参数是原因
required: true,
default: false
}
},
destroyed: function () {
this.selected = '';
},
methods: {
closeThis: function () {
this.show = false;
},
change: function (val) {
this.selected = val;
},
submit: function () {
if(this.selected === '') {
_.toast('请选择或填写一个放弃原因！', 'text', 1500, 'middle')
return false;
}
if(typeof this.$parent[this.submit_function] === 'function') {
return this.$parent[this.submit_function].apply(this.submit_function, [this.selected]);
} else {
_.toast('代码错误，没有下单回调方法！！！')
}
},
}
}




