
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Masker from 'vux/src/components/masker/index.vue'

import {mapActions} from 'vuex'
import AppHeader from '../components/Header.vue'
import api from '../utils/api'
import * as _ from '../utils/tool'


export default {
components: {
AppHeader,
XButton,
Group,
Masker,
},
data:       function () {
return {
server_data:  [],            //可以做的任务
data_noting:  false, //已全部加载完成
data_loading: false,        //数据是否正在加载？
page:         1,
done: function () { }       //默认done函数
}
},
mounted:    function () {
this.loadData();
},
methods:    {
...mapActions({
updateIsLoading: 'updateIsLoading'
}),
formatYen:   function (yen) {
return _.formatYen(yen);
},
touch:       function (item) {
/**
* item.type
*
* 1: 任务佣金
* 2: 充值
* 3: 收徒佣金
* 4: 提现扣款
* 5: 任务超时扣款
* 6: 冻结
* 7: 解除冻结
*/


if (item && item.hasOwnProperty('type')) {

}

},
refreshData: function (done) {
if (this.data_loading) {
return;
}
this.page         = 1;
this.data_nothing = false;

this.loadData(done, true)
},
loadData:    function (done, reload) {
this.done = done;
if (this.data_loading) {
return;
}
this.data_loading = true;

api.getWorkerBill({page: this.page}).then(
res => {
if (res.data.length > 0) {
if (reload) {
this.$set(this, 'server_data', res.data);
} else {
this.$set(this, 'server_data', this.server_data.concat(res.data));
}
++this.page;
this.endDone();
} else {
this.endDone(true);
}
},
p_obj => {
this.endDone(done);
}
)

},
endDone:     function (is_done, not_reset_loading) {
if (!not_reset_loading) {
this.data_loading = false;
}
if (typeof this.done === 'function') {
if (is_done) {
this.done(true);
} else {
this.done();
}
}
},
}
}



