
import Group from 'vux/src/components/group/index.vue'

import api from '../utils/api'

export default {
components: {
Group,
},
data: function () {
return {
server_data: []
}
},
mounted: function () {
this.loadData();
},
methods: {
loadData: function () {
api.getTaskList(2, 1, 1).then(
res=>{
if(res.data && res.data.length > 0) {
this.$set(this, 'server_data', res.data)
}
},
p_obj=>{

}
)
}
}
}




