
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import api from '../utils/api'
import * as _ from '../utils/tool'

export default {
directives: {
TransferDom
},
components: {
XDialog,
},
data:       function () {
return {
show:      false,
password: '',
password_old: '',
}
},
methods:    {
send: function () {
if(!this.password_old) {
_.toast('原密码不能为空！');
return false;
}

if(!this.password || this.password.length < 6) {
_.toast('新密码长度不能小于6位！');
return false;
}

if(this.password === this.password_old) {
_.toast('新密码不能与旧密码相同！');
return false;
}

let params = {
password: this.password,
password_old: this.password_old
};
api.changePassword(params).then(
res => {
_.toast('恭喜，修改成功！', 'text', 2000, 'middle');
this.show = false;
},
p => {

}
)
}
},
destroyed:  function () {

}
}



