
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'

import AppHeader from '../components/GridHeader'
import AppAnnouncement from '../components/Announcement.vue'
import AppHomeActionGrid from '../components/HomeActionGrid.vue'
import AppFooter from '../components/Footer.vue'
import api from '../utils/api'
import * as _ from '../utils/tool'
import { mapActions, mapGetters } from 'vuex'


export default {
components: {
Tab,
TabItem,
Cell,
Marquee,
MarqueeItem,
AppAnnouncement,
AppHeader,
AppHomeActionGrid,
AppFooter,
},
data () {
return {
tag:     '电商',
index:   0,
}
},
ready () {
},
mounted(){
this.getProfileInfo();
},
computed: {
...mapGetters({
profile: 'getProfile'
})
},
methods:    {
...mapActions({
updateProfile: 'updateProfile'
}),
getProfileInfo: function () {
api.getProfile().then(
res => {
if (!$.isEmptyObject(res.data)) {
this.updateProfile(this.profile)
}
},
p_obj => {

}
);
},
formatYen:      function (yen) {
return _.formatYen(yen)
}
}
}



